import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
// @material-ui/icons
import Favorite from "@material-ui/icons/Favorite";
// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Button from "components/CustomButtons/Button.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Parallax from "components/Parallax/Parallax.jsx";

import landingPageStyle from "assets/jss/material-kit-pro-react/views/landingPageStyle.jsx";

// Sections for this page
import SectionProduct from "./Sections/SectionProduct.jsx";
import SectionTeam from "./Sections/SectionTeam.jsx";
import SectionWork from "./Sections/SectionWork.jsx";
import SectionKakao from "./Sections/SectionKakao";
import SectionYoutube from "./Sections/SectionYoutube";

//
import { Element, scroller } from "react-scroll";
import ReactPixel from "react-facebook-pixel";

class LandingPage extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;

    ReactPixel.init("827253587628198");
    ReactPixel.pageView();
    this.track("ViewContent");
  }
  track = pe => {
    window.console.log("pixel", pe);
    ReactPixel.track(pe);
  };
  render() {
    const goSection = (e, dom) => {
      e.preventDefault();
      scroller.scrollTo(dom, {
        duration: 800,
        delay: 0,
        smooth: "easeInOutQuart"
      });
    };

    const { classes, ...rest } = this.props;
    return (
      <div>
        <Header
          color="transparent"
          brand="BackGround"
          links={<HeaderLinks dropdownHoverColor="info" />}
          fixed
          changeColorOnScroll={{
            height: 300,
            color: "info"
          }}
          {...rest}
        />
        <Parallax image={require("assets/img/BG.1.jpg")} filter="dark">
          <div className={classes.container}>
            <GridContainer>
              <GridItem xs={12} sm={6} md={6}>
                <h1 className={classes.title}>
                  Let's
                  <br /> board game!
                </h1>
                <h4>
                  새로 생겨난 보드 게임 모임입니다
                  <br />
                  편하게 놀다 갈 수 있는 모임이니까 아래 자세한 내용들을
                  알아보세요!
                </h4>
                <br />
                <Button
                  color="danger"
                  size="lg"
                  rel="noopener noreferrer"
                  onClick={e => goSection(e, "form")}
                >
                  바로 참여
                </Button>
              </GridItem>
            </GridContainer>
          </div>
        </Parallax>
        <div className={classNames(classes.main, classes.mainRaised)}>
          <div className={classes.container}>
            <SectionProduct fnTrack={this.track} />
            <Element name="youtube" />
            <SectionYoutube fnTrack={this.track} />
            <SectionTeam fnTrack={this.track} />
            <Element name="form" />
            <SectionWork fnTrack={this.track} />
            <Element name="kakao" />
            <SectionKakao fnTrack={this.track} />
          </div>
        </div>
        <Footer
          content={
            <div>
              <div className={classes.left} />
              <div className={classes.right}>
                &copy; {1900 + new Date().getYear()} , made with woorian by{" "}
                <a href="https://www.creative-tim.com">Creative Tim</a> for a
                better web.
              </div>
            </div>
          }
        />
      </div>
    );
  }
}

export default withStyles(landingPageStyle)(LandingPage);
