import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import { CircularProgress } from "@material-ui/core";
import { pink } from "@material-ui/core/colors";

// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import CustomInput from "components/CustomInput/CustomInput.jsx";
import Button from "components/CustomButtons/Button.jsx";

import workStyle from "assets/jss/material-kit-pro-react/views/landingPageSections/workStyle.jsx";

//
import FirebaseStore from "firebaseStore";
import swal from "sweetalert2";

class SectionWork extends React.Component {
  constructor() {
    super();
    this.state = {
      phone: "",
      loading: false
    };
  }

  setPhone = e => {
    this.setState({
      ...this.state,
      phone: e.target.value
    });
  };

  send = e => {
    e.preventDefault();

    this.props.fnTrack("CompleteRegistration");

    if (this.state.phone == "") {
      swal.fire("Sorry...", "연락처를 입력해주세요", "error");
    } else {
      this.setState({
        ...this.state,
        loading: true
      });

      const db = FirebaseStore.firestore();
      const qr = db.collection("user").add({
        phone: this.state.phone
      });
      window.console.log(qr);
      this.setState({
        ...this.state,
        loading: false,
        phone: ""
      });
      swal.fire(
        "Thank you!",
        "감사합니다! 곧 연락을 드리겠습니다 :)",
        "success"
      );
    }

    // this.state.phone
  };
  render() {
    const { classes, fnTrack } = this.props;
    const { loading } = this.state;
    return (
      <div className={classes.section}>
        <GridContainer justify="center">
          <GridItem cs={12} sm={8} md={8}>
            <h2 className={classes.title}>What's your phone number?</h2>
            <h4 className={classes.description}>
              전화번호는 오직 안내를 위해서만 사용됩니다.
              <br />
              전화번호 노출이 싫으시다면{" "}
              <a
                onClick={() => fnTrack("CompleteRegistration")}
                target={"_blank"}
                href="http://pf.kakao.com/_xfnxotj/chat"
              >
                카카오 플러스 문의
              </a>{" "}
              버튼을 클릭해주세요 :)
              <br />
              SEND를 클릭하시면
              <a
                target={"_blank"}
                href="https://docs.google.com/document/d/e/2PACX-1vTCoxaZz94YFch17ikiatmWIOUhFOSuLet8zkf4lgkCVkH-cQYnudicG2itVodnGTjP2K-LVbMXqXbj/pub"
              >
                개인 정보 취급 방침
              </a>
              에 동의합니다.
            </h4>
            <form>
              <GridContainer>
                <GridItem
                  xs={11}
                  sm={5}
                  md={5}
                  className={`${classes.mrAuto} ${classes.mlAuto}`}
                >
                  <CustomInput
                    inputProps={{ onChange: this.setPhone }}
                    formControlProps={{
                      fullWidth: true
                    }}
                    labelText="Phone Number"
                    id="phone"
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem
                  xs={12}
                  sm={4}
                  md={4}
                  className={`${classes.mrAuto} ${classes.mlAuto}`}
                >
                  <Button
                    onClick={this.send}
                    style={{
                      width: "100%"
                    }}
                    color="primary"
                    disabled={loading}
                  >
                    Send
                  </Button>
                  {loading && (
                    <CircularProgress
                      size={24}
                      style={{
                        color: pink[500],
                        position: "absolute",
                        top: "50%",
                        left: "50%",
                        marginTop: -12,
                        marginLeft: -12
                      }}
                    />
                  )}
                </GridItem>
              </GridContainer>
            </form>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

export default withStyles(workStyle)(SectionWork);
