import React from "react";
import ReactPixel from "react-facebook-pixel";

class RPixel extends React.Component {
  componentDidMount() {
    window.alert(1);
    ReactPixel.init("827253587628198");
    ReactPixel.pageView();
    this.track("ViewContent");
  }

  track = pe => {
    ReactPixel.track(pe);
  };

  render() {
    return;
  }
}

export default RPixel;
